.sidebar {
  background-color: #000;
  height: 100vh;
  width: 16rem;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 999999;
  color: #fff;
  border-radius: 3px;
  font-family: "Work Sans", sans-serif;
}

.title {
  text-transform: uppercase;
  font-size: 1.5rem;
  text-align: center;
  padding: 1rem;
}

.tools {
  text-transform: sentencecase;
  padding: 15px;
  width: 80%;
  font-size: 12px;
}

.tools__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-bottom: 2rem;
  grid-gap: 0.5rem;
}

.icon {
  margin: 5px 0;
  width: 35px;
  height: 35px;
  padding: 5px;
  background-color: #e69800;
  border-radius: 3px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: bold;
  align-items: center;
  line-height: 0;
  font-size: 1rem;
  border: 1px solid  transparent;
  outline: 2px solid transparent;

}

.icon:hover {
  background-color: #d98500;

  outline-color: #806104;
  border: 1px solid #D39E00;
}
.icon__text {
  font-size: 9px;
}

.siebar__controller {
  background-color: #000;
  width: 40px;
  height: 40px;
  position: absolute;
  right: -15%;
  top: 2%;
  border-radius: 3px;
  cursor: pointer;
  font-size: 1.5rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(13, 255, 0);
}
